import React from 'react';

import './LiveDrawerToggleButton.css';

const LivedrawerToggleButton = props => (
    <button className="toggle-button" onClick={props.click}>
        <div className="toggle-button__line" />
        <div className="toggle-button__line" />
        <div className="toggle-button__line" />
    </button>
);

export default LivedrawerToggleButton;